@import "./mixin";

@include mobile {
  html {
    font-size: 12px;
  }
}

@include desktopOrTablet {
  html {
    font-size: 13.5px;
  }
}
body {
  position: relative;
  * {
    box-sizing: border-box;
  }
}

/*   */
body,
div,
ul,
li,
dl,
dd,
dt,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
input,
fieldset,
legend,
p,
select,
table,
th,
td,
tr,
textarea,
button,
form,
figure,
figcaption {
  margin: 0;
  padding: 0;
}

/* a   */
a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

/*   */
body,
input,
textarea,
select,
button,
table {
  font-family: "Noto Sans KR", sans-serif;
  font-family: "Noto Sans", sans-serif;
}

/*    */
em,
address {
  font-style: normal;
}
em {
  color: red;
}

/*   */
ul,
li,
ol {
  list-style: none;
}

/*    */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1rem;
  font-weight: 500;
  display: inline-block;
}

/*  */
button {
  border: 0;
}

/*   */
img,
video {
  width: 100%;
}
img {
  width: 100%;
  vertical-align: top;
}
img {
  border: 0;
} //ie10

/* clearfix */
.clearfix {
  *zoom: 1;
}
.clearfix:before,
.clearfix:after {
  display: block;
  content: "";
  line-height: 0;
}
.clearfix:after {
  clear: both;
}

/* table */
table {
  border-collapse: collapse;
}

/* IR  */
.ir_pm {
  display: block;
  overflow: hidden;
  font-size: 0;
  line-height: 0;
  text-indent: -9999px;
}
/*      (Phark Method) */
.ir_wa {
  display: block;
  overflow: hidden;
  position: relative;
  z-index: -1;
  width: 100%;
  height: 100%;
}
/*           (WA IR) */
.ir_su {
  overflow: hidden;
  position: absolute;
  width: 0;
  height: 0;
  line-height: 0;
  text-indent: -9999px;
}
/*          */
